html{
  overflow-y: hidden; 
  overflow-x: hidden;
}
  
body { 
  padding: 0;
  margin: 0;
}

.photoContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.photoGrid {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: scroll; 
  width: 100px;  
  height: 93vh;
}

.photoGrid img {
  width: 100%;
  margin: -2px 0px;
}

.sticky {
  text-align: center;
  background-color: #333333;
  height: 5vh;
  color: white;
  text-transform: uppercase;
}


